<template lang="pug">
label
  input(type="checkbox" :checked="value" :disabled="disabled" @change="$emit('input', $event.target.checked)")
  .indicator.indeterminate(v-if="indeterminate")
    .indicator2
  .indicator(v-else)
    icon.check(name="check")
  .span
    slot
</template>

<script>
export default {
  name: 'check',
  props: {
    value: Boolean,
    noHover: Boolean,
    disabled: Boolean,
    indeterminate: Boolean,
  },
}
</script>

<style scoped lang="sass">
label
  display: flex
  align-items: center
  cursor: pointer
  &:hover
    .indicator.indeterminate
      border-color: $border3-color

.indicator
  width: 16px
  height: 16px
  border: $border2
  flex: 0 0 16px
  display: flex
  align-items: center
  justify-content: center
  border-radius: $radius
  transition: $transition-default
  .indicator2
    display: none
    width: 8px
    height: 8px
    background: $primary
    border-radius: $radius
  .check
    fill: $white1
    width: 10px
input
  display: none
  &:checked ~ .indicator
    border-color: $primary
    background: $primary
    &:hover
      border-color: $primary
    &.indeterminate
      border-color: $border3-color
      background: transparent
    .indicator2
      display: block
.span
  font-size: 12px
  padding-left: 10px
</style>
